
import { defineComponent } from "vue";
import { useStore } from "vuex";
import ButtonComponent from "@components/atoms/common/ButtonComponent.vue";
import { member2 } from "@/api/index";

export default defineComponent({
  components: {
    ButtonComponent,
  },
  setup() {
    const store = useStore();
    return { store };
  },
  props: {
    roomAndUserData: Object,
  },
  mounted() {
    console.log("session mounted timer :", this.store.state.session);
    this.store.state.session?.on("signal:countdown-start", (event: any) => {
      this.startTimer();
    });
    this.store.state.session?.on("signal:countdown-stop", (event: any) => {
      this.stopTimer();
    });
    this.settings();
  },
  data() {
    return {
      //
      rule: [
        //   or  ? JSON  
        ["a", "1", "", "5"], //360
        ["b", "3", "", "5"], //180
        ["b", "1", "", "5"], //360
        ["a", "3", "", "5"], //180
        ["a", "2", "", "5"], //300
        ["b", "1", "", "5"], //180
        ["b", "2", "", "5"], //300
        ["a", "1", "", "5"], //180
        ["a", "3", "", "5"], //300
        ["b", "3", "", "5"], //300
      ],
      // 1:1
      LincolnDouglas: [
        ["a", "1", "", "360"],
        ["b", "1", "", "180"],
        ["b", "1", "  ", "420"],
        ["a", "1", " ", "180"],
        ["a", "1", "", "240"],
        ["b", "1", "", "360"],
        ["a", "1", "", "180"],
      ],
      // 2:2
      CEDA: [
        ["a", "1", "", "360"],
        ["b", "2", " ", "180"],
        ["b", "1", "", "360"],
        ["a", "1", " ", "180"],
        ["a", "2", "", "360"],
        ["b", "1", " ", "180"],
        ["b", "2", "", "360"],
        ["a", "2", "", "180"],
        ["b", "1", "", "240"],
        ["a", "1", "", "240"],
        ["b", "2", "", "240"],
        ["a", "2", "", "240"],
      ],
      // 3:3
      KarlPopper: [
        ["a", "1", "", "360"], //360
        ["b", "3", "", "180"], //180
        ["b", "1", "", "360"], //360
        ["a", "3", "", "180"], //180
        ["a", "2", "", "300"], //300
        ["b", "1", "", "180"], //180
        ["b", "2", "", "300"], //300
        ["a", "1", "", "180"], //180
        ["a", "3", "", "300"], //300
        ["b", "3", "", "300"], //300
      ],

      //  
      turn: 0,
      turnUser: "  : " + "A1 " + "",
      isBreakTime: false,
      // 
      timer: null as any,
      // inputMin: "",
      // inputSec: "",
      time: 5, // props ? store
      breakTime: 3, //  
      resetButton: false,
      edit: false,
    };
  },
  computed: {
    minutes(): string {
      const minutes = Math.floor(this.time / 60);
      return this.padTime(minutes);
    },
    seconds(): string {
      const seconds = this.time - Number(this.minutes) * 60;
      return this.padTime(seconds);
    },
  },
  watch: {
    time: function (newTime, oldTime) {
      if (newTime == 0) {
        if (this.turn < this.rule.length) {
          if (!this.isBreakTime) {
            this.isBreakTime = true;
            this.turn++;
            if (this.turn == this.rule.length) {
              this.isBreakTime = false;
              this.turnUser = " !! ";
              this.resetTimer();
              this.time = 0;
              // ***    
              member2.put(`/sessions/${this.store.state.roomId}/end`);
              console.log("");
              return;
            }
            this.turnUser =
              "" +
              "   : " +
              this.rule[this.turn][0] +
              this.rule[this.turn][1] +
              this.rule[this.turn][2];
            this.resetTimer();
            this.time = this.breakTime; //  
            this.startTimer();
          } else {
            this.isBreakTime = false;
            this.turnUser =
              "  : " +
              this.rule[this.turn][0] +
              this.rule[this.turn][1] +
              this.rule[this.turn][2];
            this.resetTimer();
            this.time = Number(this.rule[this.turn][3]);
            this.startTimer();
          }
        }
      }
      return;
    },
  },

  methods: {
    settings() {
      //   
      this.rule = this.KarlPopper;
      switch (this.roomAndUserData?.maxNumOfPeople) {
        case 2:
          this.rule = this.LincolnDouglas;
          break;
        case 4:
          this.rule = this.CEDA;
          break;
        case 6:
          this.rule = this.KarlPopper;
          break;
      }
      this.time = Number(this.rule[0][3]);
      this.turnUser =
        "  : " +
        this.rule[0][0] +
        " " +
        this.rule[0][1] +
        " " +
        this.rule[0][2];
      // this.breakTime = 20;
    },
    sendStartTimer() {
      this.store.state.session?.signal({
        data: "hello",
        to: [],
        type: "countdown-start",
      });
    },
    sendStopTimer() {
      this.store.state.session?.signal({
        data: "hello",
        to: [],
        type: "countdown-stop",
      });
    },
    startTimer() {
      this.timer = setInterval(() => this.countdown(), 1000);
      this.resetButton = true;
      this.edit = false;
    },
    stopTimer: function () {
      clearInterval(this.timer);
      this.timer = null;
      this.resetButton = true;
    },
    resetTimer: function () {
      this.time = 0;
      clearInterval(this.timer);
      this.timer = null;
      this.resetButton = false;
      // this.inputMin = "";
      // this.inputSec = "";
    },
    editTimer: function () {
      this.edit = !this.edit;
    },
    padTime: function (time: number) {
      return (time < 10 ? "0" : "") + time;
    },
    countdown: function () {
      if (this.time > 0) {
        this.time--;
      } else {
        this.resetTimer();
      }
    },
  },
});
