
import { defineComponent } from "vue";
export default defineComponent({
  components: {},
  props: {
    labelId: String,
    labelValue: String,
    selector: String,
    disabled: Boolean,
  },
  data() {
    return {
      picked: "",
    };
  },
  methods: {
    radioChange(event: Event): void {
      this.$emit("method", (event.target as HTMLInputElement).value);
    },
  },
});
